  box-sizing: border-box;
}

body {
  background-color: #2fa8cc;
  font-family: "Roboto", sans-serif;
  margin: 0;
  padding: 10px;
  font-size: 12px;
}

img {
  max-width: 7rem;
  max-height: 4rem;
}

h1, h2, h3, li, input {
  color: #002d67;
}

h1 {
  font-size: 1.4rem;
}

h2 {
  font-size: 1rem;
}

h3 {
  font-size: 0.8rem;
}

li, input, a, .copyRights {
  border-radius: 0.15rem;
  font-size: 0.7rem;
}

nav {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  gap: 0.5rem;
  white-space: nowrap;
}
nav img {
  flex: 1;
}
nav h2 {
  flex: 0.5;
}

footer {
  margin-top: 1rem;
  position: relative;
  margin-top: 2rem;
  padding-bottom: 0.1rem;
  top: auto;
  bottom: 0.1rem;
}

.btn {
  background-color: #2fa8cc;
  color: #f4f4f4;
  border: 0;
  border-radius: 10px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1), 0 6px 6px rgba(0, 0, 0, 0.1);
  padding: 14px 40px;
  font-size: 16px;
  cursor: pointer;
}
.btn:active {
  transform: scale(0.98);
}
.btn:focus {
  outline: 0;
